<template>
    <div>
        <v-data-table
            :headers="headers"
            :items="items"
            item-key="name"
            :loading="loading"
            disable-sort
            hide-default-footer
        >
            <!-- eslint-disable  -->
            <template v-slot:header.name="{ header }">
                <span class="table-header">{{ header.text}}</span>
            </template>
            <template v-slot:header.code="{ header }">
                <span class="table-header">{{ header.text}}</span>
            </template>
            <template v-slot:header.color="{ header }">
                <span class="table-header">{{ header.text}}</span>
            </template>
            <template v-slot:header.amount_using="{ header }">
                <span class="table-header">{{ header.text}}</span>
            </template>
            <template v-slot:header.amount="{ header }">
                <span class="table-header">{{ header.text}}</span>
            </template>
            <template v-slot:header.amount_sewed="{ header }">
                <span class="table-header">{{ header.text}}</span>
            </template>
            <template v-slot:header.waste_amount="{ header }">
                <span class="table-header">{{ header.text}}</span>
            </template>
            <template v-slot:header.waste_percentage="{ header }">
                <span class="table-header">{{ header.text}}</span>
            </template>
            <template v-slot:header.total_price="{ header }">
                <span class="table-header">{{ header.text}}</span>
            </template>
            <template v-slot:header.date="{ header }">
                <span class="table-header">{{ header.text}}</span>
            </template>
            <template v-slot:header.actions="{ header }">
                <span class="table-header">{{ header.text}}</span>
            </template>



           

            <template v-slot:item.name="{item}">
                <div class="d-flex justify-center">
                    <span class="text-black">{{ item.name }}</span>
                </div>
            </template>

            <template v-slot:item.code="{item}">
                <div class="d-flex justify-center">
                    <span class="text-black">#{{ item.code }}</span>
                </div>
            </template>

             <template v-slot:item.color="{item}">
                <div class="d-flex justify-center">
                    <span>{{ item.color }}</span>
                </div>
            </template>

            <template v-slot:item.amount_using="{item}">
                <div class="d-flex justify-center">
                    <span class="text-black">{{ formatPrice(item.amount_using) }} kg</span>
                </div>
            </template>

            <template v-slot:item.amount="{item}">
                <div class="d-flex justify-center">
                    <span>{{ formatPrice(item.amount) }} ta</span>
                </div>
            </template>

            <template v-slot:item.amount_sewed="{item}">
                <div class="d-flex justify-center">
                    <span class="text-black">{{ formatPrice(item.amount_sewed) }} kg</span>
                </div>
            </template>

            <template v-slot:item.waste_amount="{item}">
                <div class="d-flex justify-center">
                    <span class="text-black" :class="item.waste_percentage >= 20 ? 'text-orange' : ''">{{ formatPrice(item.waste_amount) }} kg ({{item.waste_percentage.toFixed(2)}}%)</span>
                </div>
            </template>

            <template v-slot:item.waste_percentage="{item}">
                <div class="d-flex justify-center">
                    <span class="text-black">{{ (item.amount/item.amount_using).toFixed(2) }} %</span>
                </div>
            </template>

            <template v-slot:item.total_price="{item}">
                <div class="d-flex justify-center">
                    <span class="text-black">{{ formatPrice(item.total_price) }} so`m</span>
                </div>
            </template>

            <template v-slot:item.date="{item}">
                <div class="d-flex justify-center">
                    <span class="text-black">{{ item.date }}</span>
                </div>
            </template>


            <template v-slot:item.actions="{item}">
                <div class="d-flex align-center justify-center">
                    <div class="cursor-pointer mr-2 mt-1" v-if="$can('update', 'supply_bichuv_tarixi')">
                        <img src="@/assets/icons/edit.svg" alt="edit" class="cursor-pointer">
                    </div>
                    <div class="cursor-pointer mt-1" v-if="$can('delete', 'supply_bichuv_tarixi')">
                        <img src="@/assets/icons/trush-square.svg" alt="edit-2">
                    </div>
                </div>
            </template>

        </v-data-table>

        <v-pagination
            v-model="page"
            :length="pageCount"
            total-visible="7"
            class="my-2"
        ></v-pagination>
    </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'
import { priceMixin } from '@/mixins/formatter.js';
import { loaderMixin } from '@/mixins/dateLoaderMixins.js'

export default {
    mixins: [priceMixin, loaderMixin],
    data(){
        return {
            page: 1,
            pageCount: 0,
            loading: false,
            headers: [
                {text: 'Mahsulot', value: 'name', align: 'center'},
                {text: 'Kodi', value: 'code', align: 'center'},
                {text: 'Rang kodi', value: 'color', align: 'center'},
                {text: 'Ishlatilgan kg', value: 'amount_using', align: 'center'},
                {text: 'Olingan dona', value: 'amount', align: 'center'},
                {text: 'Bichuv massasi', value: 'amount_sewed', align: 'center'},
                {text: 'Isporeniya', value: 'waste_amount', align: 'center'},
                {text: 'Nisbat', value: 'waste_percentage', align: 'center'},
                {text: 'Summa', value: 'total_price', align: 'center'},
                {text: 'Sana', value: 'date', align: 'center'},
                {text: 'Harakatlar', value: 'actions', align: 'center'},
            ],
            items: [],
        }
    },
    async mounted(){
        await this.refresh()
    },
    watch:{
        page: async function (newVal, oldVal) {
            if (newVal) {
                await this.refresh()
            }
        },
        '$store.state.main.supply_tailoring_history': {
            handler: async function(value) {
                if(value){
                    await this.refresh()
                    this.$store.commit('main/SET_UPDATE_SUPPLY_TAILORING_HISTORY', false)
                }
            },
            deep: true,
            immediate: true
        },
    },
    computed: {
        ...mapState({
            history_sewed: state => state.supply.history_sewed,
            searchValue: state => state.main.searchValue
        })
    },
    methods: {
        ...mapActions({
            fetchHistorySewed: 'supply/fetchHistorySewed'
        }),
        async refresh(){
            this.loading = true
            await this.fetchHistorySewed({page: this.page, search: this.searchValue})
            this.page = this.history_sewed.current_page;
            this.pageCount = Math.ceil(this.history_sewed.total / 10);
            this.items = this.history_sewed.data;
            this.items.forEach((elem, index) => {
                elem.name = elem.product.name;
                elem.code = elem.product.code;
                elem.date = this.$dayjs(elem.created_at).format('DD-MM-YYYY') 
            })
            this.loading = false
        },
    }
}
</script>

